@import '~antd/dist/antd.css';
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.container {
  align-items: center;
  max-width: 90%;
  margin: 0 auto;
  margin-top: 20px;
}

.wrapper-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  /* border: 1px solid red; */
}

.channel-card {
  width: 30%;
  margin: 10px;
  border-radius: 5px;
  text-align: left;
  cursor: pointer;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.2);
}

.search-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
  /* border: 1px solid red; */
}

.search-input {
  width: 100%;
  line-height: 2rem;
  border-radius: 15px;
  padding-left: 10px;
  outline: none;
  margin: 0 25px;
}

.refine-button {
  float: right;
  margin-left: 25px;
  border-radius: 10px;
}

.sort-select {
  float: left;
  margin-right: 25px;
}

ul {
  list-style: none;
  padding-left: 0;
}

ul li span {
  float: left;
  width: 80px;
}

.spaced li {
  margin-bottom: 10px;
}

.spaced span {
  float: left;
  width: 100px;
}

.ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
  color: #000;
  text-shadow: 0 0 0.25px currentColor;
}

.ant-tabs-ink-bar {
  background: rgb(230, 0, 125) !important;
}

.ant-tabs-tab:hover {
  color: rgb(230, 0, 125);
}

.refine-list li {
  cursor: pointer;
}

.favourites-container {
  background: #f5f5f5;
  height: 100%;
  display: flex;
  overflow-x: auto;
  width: 100%;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.2);
}

/* @media (max-width: 850px) {
  .container {
    margin: 20px;
  }

  .channel-card {
    width: 40%;
  }
} */

@media (max-width: 800px) {
  .container {
    margin: 20px;
  }

  .channel-card {
    width: 100%;
  }

  .search-input {
    margin: 0;
  }

  .refine-button {
    margin: 0;
  }

  .sort-select {
    margin: 0;
  }
}
